#loading-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-flow: row nowrap;
          flex-flow: row nowrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 3em;
  margin-left: auto;
  margin-right: auto;
  margin-top: 50px;
}
.loading-icon {
  width: 0.6em;
  height: 2em;
  background-color: orange;
}
.loading-icon:nth-of-type(1) {
  -webkit-animation: grow 1s -0.45s ease-in-out infinite;
          animation: grow 1s -0.45s ease-in-out infinite;
}
.loading-icon:nth-of-type(2) {
  -webkit-animation: grow 1s -0.3s ease-in-out infinite;
          animation: grow 1s -0.3s ease-in-out infinite;
}
.loading-icon:nth-of-type(3) {
  -webkit-animation: grow 1s -0.15s ease-in-out infinite;
          animation: grow 1s -0.15s ease-in-out infinite;
}
.loading-icon:nth-of-type(4) {
  -webkit-animation: grow 1s ease-in-out infinite;
          animation: grow 1s ease-in-out infinite;
}
@-webkit-keyframes grow {
0%,
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}
50% {
    -webkit-transform: scaleY(2);
            transform: scaleY(2);
}
}
@keyframes grow {
0%,
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
}
50% {
    -webkit-transform: scaleY(2);
            transform: scaleY(2);
}
}

